

.salecontent {
    display: flex; padding-bottom: 60px;
    .saleimg { width: 58%; margin-right: 2%; }
}
.saleul {
    padding-bottom:60px; 
    border-bottom: 1px solid #ddd;
    h3 { font-size:16px;}
    ul {
        padding:20px 0; 
        list-style: disc; padding-left:30px;
        li { padding: 5px 0; line-height: 1.5;}    
    }
} 




.salesection {
    h2 { padding: 60px 0 40px; font-size: 24px; font-weight: 600; }
    h3 { font-size:25px;}
    .h2span { font-size:14px; font-weight: 600; padding-bottom: 40px; display: block;}
    
    button { 
        background: none; font-family: inherit; font-size: inherit; color: #555;
        &:hover { color: #000; font-weight: 500;}
    }
    margin-top:100px; 
    .inner {
         font-weight:300; 
        .salemenu { 
            font-size:14px;
            padding-bottom:15px; border-bottom:1px solid #ddd;
            a { padding-right:10px; &:hover { text-decoration:underline; } }
            span { padding-left:10px; }
        }
        .salecont1 {
            .saletop {
                h2 { font-size:36px; padding:60px 0 30px; }
                p:nth-of-type(1) { width:60%; }
                p:nth-of-type(2) { padding:50px 0; cursor:pointer; text-decoration:underline; span{padding:0 10px;} }
            }
            .svbox {
                display:flex; 
                .saletxt {
                    width:45%; margin-left:2%;
                    p:nth-of-type(1) { font-size:14px; font-weight:600; padding:20px 0 40px; }
                    p { line-height: 2; span {font-weight:600;}}
                }
            }
            .salelist {
                padding-bottom:100px;
                .slick-arrow {
                    position:absolute; top:40%; transform:translateY(-50%);
                    background:#000; padding:10px; z-index:9; width:50px; height:50px; color:#fff;
                    border-radius: 50%;
                }
                .slick-prev{ left: 0;}
                .slick-next { right: 0;}
                .slick-dots { 
                    position:absolute; bottom: -40px; width: 100%; 
                    li {
                        display: inline-block; width: 33.33%; height: 3px;
                        &.slick-active  {
                            button {  background: #000; }
                        }
                        button { 
                            color: transparent; text-indent: -999999; overflow: hidden; width: 100%; background: #ddd;
                            height: 3px; transition: all 0.5s;
                        }
                    }
                }
            }            
        }

        .salecont2 {
            .saletxt2 {
                p { padding: 5px; }
                p:nth-child(3) { font-weight: 600; color: #333;}
            }
        }
        .salecont3 {
            .saletxt2 {
                p { padding: 5px; }
                span { font-weight: 600; color: #333; }
                ul { 
                    padding:30px 0 5px;
                    li { padding: 10px;;}
                    li:nth-child(3) { padding: 25px 10px;  margin-left: 50px; list-style: disc; }
                }
            }   
        }
        .salecont4 {
            .saletxt2 {
                p { padding: 5px; }
                p:nth-child(2) { font-weight: 600; color: #333;}
            }   
        }



    }
}