.article1 {
    .main {
        width:100%; height:100%;
        position:relative; background: #ffdb00;
    
        .image { width: 75%; }
        .text { 
            position:absolute; bottom:0; right:0;
            width: 25%; height:100%;
            .h2box { 
                position:relative;
                height:60%; 
                font-size:26px; line-height: 1.5;
                h2 {
                    position:absolute; bottom: 50px; left: 30px;
                    p { 
                        span { font-size:100px; font-weight:800; line-height: 1.2; padding:0 10px; }
                    }
                    p:nth-child(1) { background: #000; color:#fff; padding: 0 5px; display:inline-block; }
                }                   
            }
            .txtbox { 
                padding:30px 60px 50px 30px; background: #fff; 
                height: 40%;
                p { padding: 20px 0 30px; }
                a { 
                    background:#000; color:#fff; border-radius:50px; padding:10px 15px; display:inline-block; 
                    font-weight:600;
                }
            }
        }
    }

}

.article2 {
    border-top: 20px solid #eee;
    .inner {
        position: relative;
        overflow: hidden; padding-bottom: 100px;
        h2 { padding: 60px 0 40px; font-size: 24px; font-weight: 600; }
        h3 {  font-size: 16px; }
        ul {
            width: 170%; transition: all 0.5s;
            li { 
                float: left; width: 20%; padding-right: 30px;
                .text {
                    position: relative;
                    padding: 30px; background: #fee002; height: 261px;
                    h3 { font-size: 20px; font-weight: 600;}
                    button { 
                        position: absolute; bottom: 30px; left: 30px;
                        background: #000; color: #fff; font-size: 24px; 
                        padding: 10px 10px 5px ; margin-top: 30px; border-radius: 50px;
                    }
                } 
                .text1 { background: #f3e5ce; }
                .text2 { background: #ef4224; }
                .text3 { background: #9aadb6; }
                .text4 { background: #009946; } 
            }
            &.move { transform: translate3d(-40%, 0, 0); }
        }
        .Left, .Right { 
            position: absolute; top: 50%; transform: translateY(-50%);
            background: #000; color: #fff; font-size: 24px; 
            padding: 10px 10px 5px ; margin-top: 30px; border-radius: 50px;
            transition: all 0.5s;
        }
        .Left { left: 0;}
        .Right { right: 0;}

        .bar {
            position: absolute; bottom: 65px; left: 20px; 
            width: 97%; height: 3px; background: #ddd;
            .stick { 
                position: absolute; transform: translate3d(0,0,0); 
                width: 60%; height: 3px; background: #000;
                transition: all 0.5s;
                &.move { transform: translate3d(67%,0,0); }
            }
            
        }
    }
}

.article3 {
    border-top: 20px solid #eee; cursor: pointer; 
    .inner {
        position: relative;
        overflow: hidden; padding-bottom: 100px;
        h2 { padding: 60px 0 40px; font-size: 24px; font-weight: 600; }
        .slick-arrow {
            position:absolute; top:40%; transform:translateY(-50%);
            background:#000; padding:10px; z-index:9; width:50px; height:50px; color:#fff;
            border-radius: 50%;
        }
        .slick-prev{ left: 0;}
        .slick-next { right: 0;}
        .slick-dots { 
            position:absolute; bottom: -40px; width: 100%; 
            li {
                display: inline-block; width: 33.33%; height: 3px;
                &.slick-active  {
                    button {  background: #000; }
                }
                button { 
                    color: transparent; text-indent: -999999; overflow: hidden; width: 100%; background: #ddd;
                    height: 3px; transition: all 0.5s;
                }
            }
            
        }

    }
}

.article4 { 
    border-top: 20px solid #eee;
    .inner {
        padding-bottom: 100px;
        h2 {
            padding: 60px 0 40px; 
            display: flex; justify-content: space-between;
            span { font-size: 24px; font-weight: 600;  }
            a { 
                font-size: 14px; padding: 10px 20px;
                border: 1px solid #ddd;
                border-radius: 50px;
                &:hover { border: 1px solid #aaa; }
            }
        }
        .arti4imgbox {
            display: flex; 
            
            .dot { 
                background: rgba(0,0,0,0.4); 
                width: 35px; height: 35px; border-radius: 50%;
                position: relative; transition: all 0.5s;
                border: 2px solid rgba(255,255,255,0);
                span { 
                    background: #fff; 
                    width: 14px; height: 14px; border-radius: 50%;
                    box-shadow: 0 0 5px #333;
                    position: absolute; top: 50%; left: 50%; transform: translate(-50%,-50%);
                }
                .dottxt { 
                    background: #fff; box-shadow: 0 0 5px #333; 
                    padding: 15px; opacity: 0; transition: all 0.5s;
                    position: absolute; top: 110%; 
                }
                &:hover { border: 2px solid rgba(255,255,255,0.6);}
                &:hover > span { width: 10px; height: 10px; }
                &:hover > .dottxt { opacity: 1;}
            }
            
            
            .arti4img1 {
                width: 49%;
                margin-right: 2%;
                position: relative;
                .dotbox1 {
                    position: absolute;
                    top: 50%; left: 65%;
                }
                .dotbox2 {
                    position: absolute;
                    top: 65%; left: 45%;
                }
            }
            .arti4img2 {
                width: 49%; position: relative;
                .dotbox3 {
                    position: absolute;
                    top: 40%; left: 56.5%;
                }
                .dotbox4 {
                    position: absolute;
                    top: 60%; left: 45%;
                }
            }
        } 
    }
}

.article5 {
    border-top: 20px solid #eee;
    .inner {
        padding-bottom: 100px;
        h2 {
            padding: 60px 0 40px;  font-size: 24px; font-weight: 600; 
        }
    }
}